import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
	data() {
		return {
			payPassword: ''
		};
	},
	methods: {
		pass() {
			let that = this;
			if (this.payPassword.length == 6) {
				uni.request({
					url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/insert/user/paypassword',
					method: 'PUT',
					data: {
						id: this.userId,
						password: this.password
					},
					success: res => {
						uni.showToast({
							title: '成功'
						});
						setTimeout(() => {
							uni.setStorage({
								key: 'payment_password',
								data: that.payPassword,
								success: function() {
									uni.navigateBack({});
								}
							});
						}, 1000);
					},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.showToast({
					title: '请输入六位数字密码'
				});
			}
		}
	}
};
